import Constants from "expo-constants";

export function getCurrentVersionTimestamp(): number | undefined {
  return Constants?.expoConfig?.extra?.buildRunTimestamp;
}

export function getCurrentVersion(): string | undefined {
  return Constants?.expoConfig?.version;
}

export function compareVersions(v1: string, v2: string): number {
  const v1Parts = v1.split(".");
  const v2Parts = v2.split(".");
  try {
    for (let i in v1Parts) {
      const v1Part = Number.parseInt(v1Parts?.[i] ?? "0");
      const v2Part = Number.parseInt(v2Parts?.[i] ?? "0");
      if (v1Part - v2Part != 0) {
        return v1Part - v2Part;
      }
    }
  } catch (e) {
    // Failed to compare versions. Pretend they are equal
    return 0;
  }
  return 0;
}
