import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
} from "react";
import { StyleSheet, useWindowDimensions, View } from "react-native";
import { useTheme } from "react-native-elements";
import {
  TooltipContent,
  TooltipContext,
  TooltipControlContext,
} from "./TooltipContext";
import { VisibilityContext } from "./VisibilityContext";

export const TooltipDisplay = () => {
  const tooltipContext = useContext(TooltipContext);
  const theme = useTheme();
  const windowDimensions = useWindowDimensions();
  return useMemo(() => {
    return (
      <View
        style={{
          zIndex: 800,
          display: tooltipContext.tooltip != null ? undefined : "none",
          position: "absolute",
          left: tooltipContext.position?.left,
          top: tooltipContext.position?.top,
          bottom: tooltipContext.position?.bottom,
          right: tooltipContext.position?.right,
          minWidth: tooltipContext.position.containerWidth,
          alignItems: "center",
        }}
        pointerEvents="none"
      >
        <View
          style={{
            padding: 8,
            alignSelf: "center",
            backgroundColor: theme.theme.colors.background,
            borderRadius: 4,
            borderWidth: 1,
            borderColor: theme.theme.colors.grey3,
            maxWidth: windowDimensions.width / 3,
          }}
          pointerEvents="none"
        >
          {tooltipContext.tooltip}
        </View>
      </View>
    );
  }, [
    tooltipContext.tooltip,
    JSON.stringify(tooltipContext.position),
    theme,
    windowDimensions.width,
  ]);
};

export const TooltipWrapper = (props: {
  children: any;
  tooltip: TooltipContent;
  tooltipId: string;
  containerStyle?: any;
}) => {
  const tooltipContext = useContext(TooltipControlContext);
  const visible = useContext(VisibilityContext);

  const screenDimensions = useWindowDimensions();

  const wrapperRef = useRef<View | null>(null);

  const measureAndCall = useCallback(
    (cb: () => void) => {
      wrapperRef.current?.measure((x, y, width, height, pageX, pageY) => {
        if (pageY > screenDimensions.height / 2) {
          tooltipContext.setTooltip(props.tooltipId, props.tooltip, {
            left: pageX,
            containerWidth: width,
            bottom: screenDimensions.height - pageY + 4,
          });
        } else {
          tooltipContext.setTooltip(props.tooltipId, props.tooltip, {
            top: pageY + height + 4,
            left: pageX,
            containerWidth: width,
          });
        }
        cb();
      });
    },
    [
      tooltipContext.setTooltip,
      props.tooltip,
      props.tooltipId,
      wrapperRef,
      screenDimensions.height,
    ],
  );

  const onMouseOver = useCallback(() => {
    measureAndCall(() => tooltipContext.incrementRefcount(props.tooltipId));
  }, [props.tooltipId, measureAndCall]);
  const onMouseOut = useCallback(() => {
    tooltipContext.decrementRefcount(props.tooltipId);
  }, [props.tooltipId]);

  useEffect(() => {
    measureAndCall(() => {});
  }, [measureAndCall]);

  useEffect(() => {
    if (!visible) {
      tooltipContext.onUnmountTooltip(props.tooltipId);
    }
  }, [visible, tooltipContext.clearTooltip, props.tooltipId]);

  useEffect(() => {
    if (props.tooltip == null) {
      tooltipContext.clearTooltip(props.tooltipId);
    }
  }, [props.tooltip, props.tooltipId, tooltipContext.clearTooltip]);

  useEffect(() => {
    measureAndCall(() => {});
    return () => tooltipContext.onUnmountTooltip(props.tooltipId);
  }, [props.tooltipId]);

  return useMemo(
    () => (
      <View
        ref={wrapperRef}
        style={[styles.tooltipWrapper, props.containerStyle]}
        // @ts-expect-error React Native Web supports this
        onMouseEnter={onMouseOver}
        onMouseLeave={onMouseOut}
      >
        {props.children}
      </View>
    ),
    [wrapperRef, props.children, props.containerStyle, onMouseOver, onMouseOut],
  );
};

const styles = StyleSheet.create({
  tooltipWrapper: {
    alignSelf: "stretch",
    flex: 1,
  },
});
